import React, { useRef, useEffect, useState } from 'react';

const GifOverlay = ({ gif, currentTime, videoDuration, onUpdate, onSelect, isSelected }) => {
  const containerRef = useRef(null);
  const [isDragging, setIsDragging] = useState(false);
  const dragOffset = useRef({ x: 0, y: 0 });

  // 滑鼠事件：拖曳開始
  const handleMouseDown = (e) => {
    e.preventDefault();
    setIsDragging(true);
    onSelect(gif.id);
    dragOffset.current = {
      x: e.clientX - gif.transform.x,
      y: e.clientY - gif.transform.y,
    };
  };

  // 滑鼠事件：拖曳進行中
  const handleMouseMove = (e) => {
    if (!isDragging) return;
    const newX = e.clientX - dragOffset.current.x;
    const newY = e.clientY - dragOffset.current.y;
    onUpdate({
      ...gif,
      transform: { ...gif.transform, x: newX, y: newY },
    });
  };

  // 滑鼠事件：拖曳結束
  const handleMouseUp = () => {
    setIsDragging(false);
  };

  // 觸控事件：拖曳開始
  const handleTouchStart = (e) => {
    e.preventDefault();
    setIsDragging(true);
    onSelect(gif.id);
    const touch = e.touches[0];
    dragOffset.current = {
      x: touch.clientX - gif.transform.x,
      y: touch.clientY - gif.transform.y,
    };
  };

  // 觸控事件：拖曳進行中
  const handleTouchMove = (e) => {
    if (!isDragging) return;
    const touch = e.touches[0];
    const newX = touch.clientX - dragOffset.current.x;
    const newY = touch.clientY - dragOffset.current.y;
    onUpdate({
      ...gif,
      transform: { ...gif.transform, x: newX, y: newY },
    });
  };

  // 觸控事件：拖曳結束
  const handleTouchEnd = () => {
    setIsDragging(false);
  };

  // 監聽滑鼠拖曳事件
  useEffect(() => {
    if (isDragging) {
      window.addEventListener('mousemove', handleMouseMove);
      window.addEventListener('mouseup', handleMouseUp);
    } else {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('mouseup', handleMouseUp);
    }
    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('mouseup', handleMouseUp);
    };
  }, [isDragging]);

  return (
    <div
      ref={containerRef}
      className={`gif-container ${isSelected ? 'selected' : ''}`}
      style={{
        position: 'absolute',
        left: `${gif.transform.x}px`,
        top: `${gif.transform.y}px`,
        cursor: 'grab',
        zIndex: isSelected ? 11 : 10,
        display: currentTime >= gif.startTime && currentTime <= gif.endTime ? 'block' : 'none',
      }}
      onMouseDown={handleMouseDown}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
      onTouchCancel={handleTouchEnd}  // 當觸控中斷時也停止拖曳
    >
      {/* 內層 div 處理縮放與旋轉 */}
      <div
        style={{
          transform: `scale(${gif.transform.scale}) rotate(${gif.transform.rotate}deg)`,
          transformOrigin: 'center center',
        }}
      >
        <img src={gif.url} alt="gif-overlay" style={{ display: 'block' }} />
      </div>
    </div>
  );
};

export default GifOverlay;
