import React from 'react';
import GifTimeline from './GifTimeline';

const GifControls = ({ gifs, selectedGifId, videoDuration, onUpdate }) => {
  const selectedGif = gifs.find((gif) => gif.id === selectedGifId);
  if (!selectedGif) return null;

  return (
    <div
      className="gif-controls"
      style={{
        position: 'relative',
        marginLeft: '20px',
        display: 'flex',
        flexDirection: 'column',
        gap: '10px',
        padding: '10px',
        border: '1px solid #ddd',
        backgroundColor: '#f9f9f9',
        borderRadius: '5px',
      }}
    >
      <div style={{ border: '1px solid #ccc', padding: '10px', borderRadius: '5px' }}>
        {/* 時間軸控制 `startTime` 與 `endTime` */}
        <GifTimeline
          step={0.1}
          startTime={selectedGif.startTime || 0}
          endTime={selectedGif.endTime || videoDuration}
          videoDuration={videoDuration}
          onChange={(newStart, newEnd) => {
            onUpdate({ ...selectedGif, startTime: newStart, endTime: newEnd });
          }}
        />
        <div>
          <label>Rotate:</label>
          <input
            type="range"
            min="-360"
            max="360"
            value={selectedGif.transform.rotate}
            onChange={(e) => {
              const newRotate = parseFloat(e.target.value);
              onUpdate({
                ...selectedGif,
                transform: { ...selectedGif.transform, rotate: newRotate },
              });
            }}
          />
        </div>
        <div>
          <label>Scale:</label>
          <input
            type="range"
            min="0.1"  // 確保 scale 不會變成 0，避免 GIF 消失
            max="3"
            step="0.1"
            value={selectedGif.transform.scale}
            onChange={(e) => {
              const newScale = parseFloat(e.target.value);
              onUpdate({
                ...selectedGif,
                transform: { ...selectedGif.transform, scale: newScale },
              });
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default GifControls;
